						}
						if (!getApp().globalData.openid) { //是否有openid
							if (!this.qr) {
								return false;
							}
							this.$Store.dispatch('getOpenid').then(res => {
								let [e, r] = res;
								if (r) { //有openid
									getApp().globalData.openid = r.data.openid;
									uni.getStorage({ //查询是否查看隐私说明
										key: 'readPrivacy'
