												e.errMsg == 'getLocation:fail:auth denied') {
												this.needLocation = true;
											} else {
												this.noLocation = true;
											}
										}
									})
								}
							})
						} else { //重新获取
							console.log(e)
							uni.navigateTo({
								url: '../tool/getOpenid/getOpenid?type=openid'
							})
						}
					})
				} else {
					if (!this.qr) {
						return false;
					}
					console.log(getApp().globalData.openid)
					uni.getStorage({
						key: 'readPrivacy'
